@import url(https://fonts.googleapis.com/css2?family=Radio+Canada:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #303030;
  color: #FFFFFF;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Customize your main colors in :root variables */


:root {
  --main-background-color: #343A50;
  --card-background-color: #804980;
  --countdown-background-color: #433765;
  --main-text-color:#F7F6F4;
  --title-text-color:#FF89B0;
}

body{
/*  background-color:var(--main-background-color);*/
  background: url(/static/media/background.195ad2b7.png);
  background-size: cover;
  font-family: 'Radio Canada', cursive;
  font-size: 1.1em;
}

h2, p{
  color: #F7F6F4;
  color: var(--main-text-color);
}

h3{
font-weight: normal;
}




